/*********************
PLUGIN STYLES
*********************/
// Makes Gravity Forms play nice with Foundation Form styles
.gform_body ul {
    list-style: none outside none;
    margin: 0;
}

// Hide Gravity Forms validation fields when GFCSS is disabled
.gform_validation_container,
.gform_wrapper .gform_validation_container,
body .gform_wrapper li.gform_validation_container,
body .gform_wrapper .gform_body ul.gform_fields li.gfield.gform_validation_container,
body .gform_wrapper ul.gform_fields li.gfield.gform_validation_container {
    display: none !important;
    position: absolute !important;
    left: -9000px;
}

.margin-auto {
	margin: 0 auto;
}

.grid-margin-x>.large-10 {
	@media screen and (min-width: 769px) {
		width: 85%;
	}
}
.grid-x>.large-10 {
	@media screen and (min-width: 769px) {
		justify-content: center;
	}
}

button,
.button {
	font-size: 14px;
	font-style: normal;
	font-weight: 800;
	letter-spacing: 1px;
	border-radius: 56px;
	padding: 18px 40px;
	justify-content: center;
	align-items: center;
	text-transform: uppercase;
	cursor: pointer;

	&.white {
		background-color: $white;
		color: $dark-blue;
		border: 2px solid transparent;

		&:hover {
			background-color: $sky-blue;
			color: $white;
			border: 2px solid $white;
		}
	}

	&.sky-blue {
		background-color: $sky-blue;
		color: $white;
		border: 2px solid transparent;

		&:hover {
			background-color: $white;
			color: $sky-blue;
			border: 2px solid $sky-blue;
		}
	}
	
	&.red {
		background-color: $red;
		color: $white;
		border: 2px solid transparent;

		&:hover {
			background-color: $white;
			color: $red;
			border: 2px solid $red;
		}
	}

	&.white-red {
		background-color: $white;
		color: $red;
		border: 2px solid $red;

		&:hover {
			background-color: $red;
			color: $white;
		}
	}

	&.dark-blue-white {
		background-color: $dark-blue;
		color: $white;
		border: 2px solid $white;

		&:hover {
			background-color: $white;
			color: $dark-blue;
			border: 2px solid $dark-blue;
		}
	}

}

.flex {
	@media screen and (min-width: 769px) {
		display: flex;
	}
}

.full-width {
	display: block;
    position: relative;
    width: 100%;
}

.content {
	position: relative;
	width: 100%;
	margin: 40px auto;

	.page-template-page-home & {
		margin: 0 auto 40px;
	}
}

.center {
	text-align: center;
}

.home-page-container {
	margin-bottom: 80px;
}

.menu .button, .menu a {
	padding: unset;
}

.sky-blue-link {
	a {
		font-size: 14px;
		font-style: normal;
		font-weight: 800;
		letter-spacing: 1px;
		border-radius: 56px;
		padding: 18px 40px !important;
		justify-content: center;
		align-items: center;
		text-transform: uppercase;
		background-color: $sky-blue;
		color: $white;
		border: 2px solid transparent;

		&:hover {
			background-color: $white;
			color: $sky-blue;
			border: 2px solid $sky-blue;
		}
	}
}

.menu-item {
	padding: 18px 20px;

	&:not(.sky-blue-link) {
		font-size: 14px;
		font-style: normal;
		font-weight: 800;
		line-height: 16px; /* 114.286% */
		letter-spacing: 1px;
		text-transform: uppercase;
		color: $white;
		
		a {
			color: $white;
			line-height: 54px;
			border-bottom: transparent 0.125em solid;

			&:hover {
				border-bottom: $sky-blue 0.125em solid;
			}
		}
	}
}

.top-bar, .top-bar ul {
	background-color: $dark-blue;
}

.custom-logo-link {
	width: 186px;
	
	@media screen and (min-width: 769px) {
		width: 280px;
	}
}

.menu .active>a, .menu .is-active>a  {
	background: transparent;
	border-bottom: $sky-blue 0.125em solid;
}

.top-bar {
	height: 80px;

	@media screen and (min-width: 769px) {
		height: 110px;
	}
}