/**
 * YouTube Video Overlay Styles
 */
.youtube-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.95);
  z-index: 9999;
  display: none;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
}
.youtube-overlay .overlay-content {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: calc(100vw - 320px);
  max-height: calc(100vh - 400px);
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 768px) {
  .youtube-overlay .overlay-content {
    max-width: 100%;
    max-height: 100%;
    padding: 20px;
  }
}
@media screen and (min-width: 769px) {
  .youtube-overlay .overlay-content {
    max-width: unset;
    max-height: unset;
    padding: 2%;
  }
}
.youtube-overlay .close-button {
  position: absolute;
  top: -5px;
  right: 0;
  background: none;
  border: none;
  color: white;
  font-size: 40px;
  line-height: 1;
  cursor: pointer;
  z-index: 10001;
  padding: 4px;
  font-weight: 400;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  transition: background-color 0.2s ease;
}
.youtube-overlay .close-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.youtube-overlay .close-button:focus {
  outline: 2px solid white;
  outline-offset: 2px;
}
@media screen and (max-width: 768px) {
  .youtube-overlay .close-button {
    top: 6px;
    right: -10px;
    font-size: 40px;
    width: 50px;
    height: 50px;
  }
}
.youtube-overlay .video-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #000;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
